.page_result {
  padding: 0;

  .preview {
    flex-grow: 5;
  }

  &__bottom {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    padding: .75em;
    flex-grow: 1;
    background: linear-gradient(167.95deg, #6C23C8 -64.75%, #0D40C5 -64.73%, #EF59B3 120.73%);
  }

  &__emotion {
    margin-top: 1.5em;
    font-size: 3.2em;
  }

  &__text {
    max-width: 200px;
    margin: 0 auto 1em;
    text-align: center;
    line-height: 1.3;
  }

  &__body {
    padding: .8em .9em .5em;
    color: #9A9AA0;
  }

  &__charts {
    display: flex;
  }

  &__chart {
    width: calc(100%/3);
    padding: 0 .25em;
    position: relative;
    &:before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: .55em;
      height: .55em;
      border: .05em solid #C3B4B4;
      border-radius: 50%;
    }
    &_active {
      &:before {
        border-color: #BA85CE;
      }
      &:after {
        content: '';
        position: absolute;
        top: .15em;
        left: .15em;
        width: .25em;
        height: .25em;
        background: #BA85CE;
        border-radius: 50%;
      }
    }
  }

  .chart {
    width: 100%;
    max-width: 87px;
    margin: 0 auto;
  }

  &__orientation {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    max-width: 81%;
    margin-top: 1em;
    padding-left: 1.9em;
    position: relative;

    .switcher {
      position: absolute;
      top: 0;
      left: 0;
    }

    &-text {
      display: block;
      margin-bottom: .2em;
      font-size: .7em;
      font-weight: 300;
      color: #8A8A8A;
    }

    .linebar {
      margin: .3em 0;
    }

  }

  &__linebars {
    width: 100%;
    max-height: 300px;
    overflow: hidden;
    transition: 1s max-height;
    &_hidden {
      max-height: 0;
      transition-duration: .5s;
    }
  }

}