html {
  box-sizing: border-box;
  font-size: 20px;
  color: #fff;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

html, body, #root {
  height: 100%;
}
body {
  min-height: 100%;
  font-family: 'Roboto', sans-serif;
}
img {
  max-width: 100%
}
h1, h2 {
  margin: 0;
}

.page {
  display: flex;
  flex-direction: column;
  height: 100%;
  overflow: scroll;
  position: relative;
}